import _app from '../../js/app.js';
import QStemplate from '../../template/template.vue';
import QSInputsMixin from '../../js/QSInputsMixin.js';

export default {
	components: {
		QStemplate
	},
	props: {
		itemArray: {
			type: Array,
			default () {
				return []
			}
		},
		color: {
			type: String,
			default: '#33cc33'
		},
		scale: {
			type: String,
			default: '.8'
		},
		disabled: {
			type: Boolean,
			default: false
		}
	},
	methods: {
		radioChange({
			detail: {
				value
			}
		}) {
			this.setValue(value);
		}
	},
	mixins: [QSInputsMixin({
		QSInputsType: _app.typeObj.radio
	})]
};
